import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import authService from '../services/authService';
import { ADMIN_EMAIL, COMPANY_DOMAIN } from '../../../config';

export const useLogin = (propEngine) => {
  const location = useLocation();
  
  // State management
  const [formData, setFormData] = useState({
    username: "",
    password: "",
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [selectedEngine, setSelectedEngine] = useState(propEngine || "11");
  const [needsVerification, setNeedsVerification] = useState(false);
  const [verificationCode, setVerificationCode] = useState("");
  const [rememberMe, setRememberMe] = useState(false);

  // Initialize engine and remember me
  useEffect(() => {
    if (location.state?.engine) {
      setSelectedEngine(location.state.engine);
    } else if (propEngine) {
      setSelectedEngine(propEngine);
    }

    const savedUsername = localStorage.getItem('rememberUsername');
    if (savedUsername) {
      setFormData(prev => ({ ...prev, username: savedUsername }));
      setRememberMe(true);
    }
  }, [location, propEngine]);

  // Handle login submission
  const handleSubmit = async (onLoginSuccess) => {
    setError("");

    if (!formData.username || !formData.password) {
      setError("ÏÇ¨Ïö©ÏûêÎ™ÖÍ≥º ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    setIsLoading(true);

    try {
      const result = await authService.signIn(formData.username, formData.password);

      if (result.success) {
        const userInfo = {
          ...result.user,
          selectedEngine: selectedEngine
        };
        
        // Store user information
        localStorage.setItem('userInfo', JSON.stringify(userInfo));
        localStorage.setItem('authToken', result.tokens.accessToken);
        localStorage.setItem('idToken', result.tokens.idToken);
        localStorage.setItem('refreshToken', result.tokens.refreshToken);
        localStorage.setItem('isLoggedIn', 'true');
        localStorage.setItem('selectedEngine', selectedEngine);
        
        // Determine user role - ONLY ai@sedaily.com gets admin access
        let userRole = 'user';
        let userPlan = 'free';

        if (formData.username === ADMIN_EMAIL || result.user.email === ADMIN_EMAIL) {
          console.log('üîê Admin email matched:', ADMIN_EMAIL);
          userRole = 'admin';
          userPlan = 'premium';
        }
        // Removed company domain check - only specific admin email gets admin access

        console.log('üîê Final userRole:', userRole);
        localStorage.setItem('userRole', userRole);
        localStorage.setItem('userPlan', userPlan);
        
        // Clear usage cache
        localStorage.removeItem('usage_percentage_11');
        localStorage.removeItem('usage_percentage_time_11');
        localStorage.removeItem('usage_percentage_22');
        localStorage.removeItem('usage_percentage_time_22');
        
        // Handle Remember Me
        if (rememberMe) {
          localStorage.setItem('rememberUsername', formData.username);
        } else {
          localStorage.removeItem('rememberUsername');
        }

        // Update header
        window.dispatchEvent(new CustomEvent('userInfoUpdated'));

        // Login success callback
        onLoginSuccess(userRole);
      } else {
        if (result.needsNewPassword) {
          setError("ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏÑ§Ï†ïÌï¥Ïïº Ìï©ÎãàÎã§. Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌïòÏÑ∏Ïöî.");
        } else {
          setError(result.error || "Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        }
      }
    } catch (err) {
      console.error("Î°úÍ∑∏Ïù∏ Ïò§Î•ò:", err);
      
      if (err.name === 'UserNotConfirmedException' || err.message?.includes('not confirmed')) {
        setNeedsVerification(true);
        setError("Ïù¥Î©îÏùº Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌï©ÎãàÎã§. Ïù∏Ï¶ù ÏΩîÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      } else {
        setError(authService.getErrorMessage(err) || "Î°úÍ∑∏Ïù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
      }
    } finally {
      setIsLoading(false);
    }
  };

  // Handle verification
  const handleVerification = async (onLoginSuccess) => {
    setError("");

    if (!verificationCode || verificationCode.length !== 6) {
      setError("6ÏûêÎ¶¨ Ïù∏Ï¶ù ÏΩîÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    setIsLoading(true);

    try {
      const result = await authService.confirmSignUp(formData.username, verificationCode);

      if (result.success) {
        // Auto login after verification
        const loginResult = await authService.signIn(formData.username, formData.password);
        
        if (loginResult.success) {
          const userInfo = {
            ...loginResult.user,
            selectedEngine: selectedEngine
          };
          
          localStorage.setItem('userInfo', JSON.stringify(userInfo));
          localStorage.setItem('authToken', loginResult.tokens.accessToken);
          localStorage.setItem('idToken', loginResult.tokens.idToken);
          localStorage.setItem('refreshToken', loginResult.tokens.refreshToken);
          localStorage.setItem('isLoggedIn', 'true');
          
          let userRole = 'user';
          let userPlan = 'free';
          if (formData.username === ADMIN_EMAIL || loginResult.user.email === ADMIN_EMAIL) {
            userRole = 'admin';
            userPlan = 'premium';
          }
          // Removed company domain check - only specific admin email gets admin access
          localStorage.setItem('userRole', userRole);
          localStorage.setItem('userPlan', userPlan);
          
          window.dispatchEvent(new CustomEvent('userInfoUpdated'));
          
          onLoginSuccess(userRole);
        } else {
          setError("Ïù∏Ï¶ùÏùÄ ÏôÑÎ£åÎêòÏóàÏßÄÎßå Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
          setNeedsVerification(false);
        }
      } else {
        setError(result.error || "Ïù∏Ï¶ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }
    } catch (err) {
      console.error("Ïù∏Ï¶ù Ïò§Î•ò:", err);
      setError("Ïù∏Ï¶ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsLoading(false);
    }
  };

  // Handle resend code
  const handleResendCode = async () => {
    setError("");
    setIsLoading(true);

    try {
      const result = await authService.resendConfirmationCode(formData.username);
      
      if (result.success) {
        alert(result.message || "Ïù∏Ï¶ù ÏΩîÎìúÍ∞Ä Ïû¨Î∞úÏÜ°ÎêòÏóàÏäµÎãàÎã§.");
      } else {
        setError(result.error || "Ïù∏Ï¶ù ÏΩîÎìú Ïû¨Î∞úÏÜ°Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }
    } catch (err) {
      console.error("Ïù∏Ï¶ù ÏΩîÎìú Ïû¨Î∞úÏÜ° Ïò§Î•ò:", err);
      setError("Ïù∏Ï¶ù ÏΩîÎìú Ïû¨Î∞úÏÜ° Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsLoading(false);
    }
  };

  // Handle input change
  const handleInputChange = (field, value) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }));
    setError("");
  };

  const handleForgotPassword = () => {
    alert("ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ï Í∏∞Îä•ÏùÄ Ï§ÄÎπÑ Ï§ëÏûÖÎãàÎã§.\nÍ¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌï¥Ï£ºÏÑ∏Ïöî.");
  };

  return {
    // State
    formData,
    isLoading,
    error,
    selectedEngine,
    needsVerification,
    verificationCode,
    rememberMe,
    
    // Actions
    handleSubmit,
    handleVerification,
    handleResendCode,
    handleInputChange,
    handleForgotPassword,
    setVerificationCode,
    setRememberMe,
    setNeedsVerification,
  };
};